@charset "utf-8";

.WishListTemplate {
  width: 520px;
  margin: 0 auto;
  padding: 0rem 1rem 4rem;
  box-shadow: 1px 5px 5px 5px #d1d1d1;
}


/* 상단 보라색 바 */
.title-bar {
  background: #673ab7;
  color: #fff;
  font-size: 2rem;
  text-align: center;
  padding: 2.2rem 0;
  font-weight: 600;
  margin-top: 30px;
}

/* 입력 영역 */
.form-wrapper {
  margin-top: -1.6rem;      /* 스샷처럼 살짝 겹쳐보이게 */
  padding: 2rem 0 1rem;
}

/* 아이템 리스트 영역 */
.item-wrapper {
  margin-top: 1.2rem;
  display: flex;
  flex-direction: column;
}


.WishItemList {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;          /* ↑ 원하는 간격(px·rem 단위) */
  padding: 0;     /* ← UL 기본 들여쓰기 제거 */
  margin: 0;      /* ← UL 기본 위·아래 여백도 없애려면 */
}

