.Form {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.Form input {
  flex: 1;
  height: 42px;
  padding: 0 1rem;
  border: 2px solid #673ab7;
  border-radius: 8px;
  font-size: 1rem;
}

.Form .add-btn {
  height: 42px;
  padding: 0 1.1rem;
  border: none;
  border-radius: 20px;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

/* 간단 팔레트 */
.palette {
  list-style: none;
  display: flex;
  margin: 0.8rem 0 0;
  padding: 0;
  gap: 0.4rem;
}

.palette li {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid transparent;
}

.palette li.active {
  border-color: #333;
}
