@charset "utf-8";

.WishItem {
  background: #fff;
  border: 2px solid #673ab7;
  border-radius: 18px;
  padding: 1rem 1.2rem 1.3rem;
  position: relative;
}

.WishItem .date {
  color: #999;
  font-size: 0.9rem;
  margin: 0 0 0.4rem;
}

.WishItem .text {
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 0.8rem;
  line-height: 1.4;
  border-bottom: 2px solid #673ab7;
  padding-bottom: 0.5rem;
}

.status-group {
  display: flex;
  gap: 1.8rem;
  margin-top: 0.8rem;
}

.status-group input {
  display: none;            /* 기본 라디오 숨김 */
}

.status-group label {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  color: #b5b5b5;           /* 기본 회색 */
  cursor: pointer;
  font-weight: 500;
  transition: color 0.2s;
}

/* 커스텀 동그라미 */
.status-group label::before {
  content: '';
  width: 12px;
  height: 12px;
  border: 2px solid #b5b5b5;
  border-radius: 50%;
  display: inline-block;
  transition: all 0.2s;
}

.status-group label.active,
.status-group label:hover {
  color: #673ab7;           /* 선택 & hover 보라색 */
  font-weight: 700;
}

.status-group label.active::before {
  background: #673ab7;
  border-color: #673ab7;
}

/* 삭제 버튼(선택) */
.del-btn {
  position: absolute;
  top: 10px;
  right: 12px;
  background: none;
  border: none;
  font-size: 1.2rem;
  color: #aaa;
  cursor: pointer;
}
.del-btn:hover {
  color: #f03e3e;
}
